import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import { trackMouse, getInterpolatedColours, getClosestColour, colorToString, color, distance } from './utils/utils';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

const colorArr = [
  getInterpolatedColours([
    color(201, 219, 250),
    color(155, 139, 248),
    color(249, 206, 252),
  ], 500),
  getInterpolatedColours([
    color(225, 100, 115),
    color(236, 170, 179),
    color(192, 50, 102),
  ], 500)
];

const shapes = [[]];
const offsets = [0];
let tracking = false;
let canClose = false;

function inRange(v1, v2, range = 10) {
  return v1 > (v2 - range) && v1 < (v2 + range);
}

const angle = (cx, cy, ex, ey) => {
  let dy = ey - cy;
  let dx = ex - cx;
  let theta = Math.atan2(dy, dx); // range (-PI, PI]
  if (theta < 0) theta = (Math.PI * 2) + theta; // range [0, PI*2)
  return theta;
}

function stopTracking() {
  offsets.push(0);
  shapes.push([]);
  tracking = false;
  canClose = false;
}

app.onRender(({ frame, width, height, debug }) => {
  ctx.fillStyle = 'white';
  ctx.fillRect(0, 0, width, height);

  if (tracking && frame % 1 === 0) {
    shapes[shapes.length - 1].push({
      x: mouseX,
      y: mouseY
    });

    if (canClose) {
      if (inRange(mouseX, shapes[shapes.length - 1][0].x) && inRange(mouseY, shapes[shapes.length - 1][0].y)) {
        shapes[shapes.length - 1].push({
          ...shapes[shapes.length - 1][0]
        });
        stopTracking();
      }
    } else {
      if (!(inRange(mouseX, shapes[shapes.length - 1][0].x, 50) && inRange(mouseY, shapes[shapes.length - 1][0].y, 50))) {
        canClose = true;
      }
    }
  }

  for (let x = 0; x < offsets.length - 1; x++) {
    offsets[x]++;
  }

  ctx.lineCap = 'round';

  for (let i = 0; i < shapes.length; i++) {
    for (let p = 1; p < shapes[i].length; p++) {
      ctx.beginPath();
      ctx.lineWidth = 10;
      // ctx.lineWidth = 20 * (angle(shapes[i][p - 1].x, shapes[i][p - 1].y, shapes[i][p].x, shapes[i][p].y) / (Math.PI * 2)) + 5;
      ctx.strokeStyle = colorToString(getClosestColour(((p + offsets[i]) % shapes[i].length) / shapes[i].length, colorArr[i % 2]));
      ctx.moveTo(shapes[i][p - 1].x, shapes[i][p - 1].y);
      ctx.lineTo(shapes[i][p].x, shapes[i][p].y);
      ctx.stroke();
    }
  }
});

el.addEventListener('click', () => {
  if (!tracking) {
    tracking = true;
  } else {
    stopTracking();
  }
})

trackMouse();
app.start();


